"use client";

import { useState } from "react";
import {
  Box,
  Button,
  Card,
  CardContent,
  CardMedia,
  Typography,
  Modal,
  Rating,
} from "@mui/material";
import styles from "./main.module.css";

type Props = {
  language: "en" | "ka";
};

type Flower = {
  nameEN: string;
  nameKA: string;
  price: string;
  img: string;
};

const flowers: Flower[] = [
  { nameEN: "Rose", nameKA: "·Éï·Éê·É†·Éì·Éò", price: "15$", img: "roses.jpg" },
  { nameEN: "Lily", nameKA: "·É®·É†·Éù·É®·Éê·Éú·Éò", price: "10$", img: "lily.jpg" },
  { nameEN: "Tulip", nameKA: "·É¢·Éò·É¢·Éê", price: "12$", img: "tulip.jpg" },
  { nameEN: "Daisy", nameKA: "·Éí·Éï·Éò·É†·Éò·Éö·Éê", price: "11$", img: "daisy.jpg" },
  { nameEN: "Chrysanthemum", nameKA: "·É•·É†·Éò·Éñ·Éê·Éú·Éó·Éî·Éõ·Éê", price: "9$", img: "Chrysanthemum.jpg" },
  { nameEN: "Carnation", nameKA: "·Éõ·Éò·ÉÆ·Éê·Éô·Éò", price: "18$", img: "Carnation.jpg" },
  { nameEN: "Lavender", nameKA: "·Éö·Éê·Éï·Éê·Éú·Éì·Éê", price: "6$", img: "Lavender.jpg" },
  { nameEN: "Orchid", nameKA: "·Éù·É†·É•·Éò·Éì·Éî·Éê", price: "11$", img: "Orchid.jpg" },
  { nameEN: "Sunflower", nameKA: "·Éõ·Éñ·Éî·Éê·Éß·Éï·Éê·Éï·Éò·Éö·Éê", price: "12$", img: "Sunflower.jpg" },
];

const MainContent = ({ language }: Props) => {
  const [open, setOpen] = useState(false);
  const [selectedFlower, setSelectedFlower] = useState<Flower | null>(null);

  const handleOpen = (flower: Flower) => {
    setSelectedFlower(flower);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setSelectedFlower(null);
  };

  return (
    <>
      <Box className={styles.main}>
        {flowers.map((item, index) => (
          <Card
            key={index}
            onClick={() => handleOpen(item)}
            sx={{
              width: 300,
              height: 400,
              borderRadius: 4,
              boxShadow: 6,
              transition: "0.3s ease",
              cursor: "pointer",
              "&:hover": {
                transform: "scale(1.03)",
                boxShadow: 10,
              },
            }}
          >
            <CardMedia
              component="img"
              height="230"
              image={item.img}
              alt={language === "en" ? item.nameEN : item.nameKA}
            />
            <CardContent>
              <Typography
                variant="h6"
                gutterBottom
                sx={{ fontWeight: "bold", fontFamily: "Georgia, serif" }}
              >
                {language === "en" ? item.nameEN : item.nameKA}
              </Typography>

              <Box
                sx={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                }}
              >
                <Typography
                  variant="h6"
                  sx={{
                    color: "crimson",
                    fontWeight: "bold",
                    fontSize: "1.2rem",
                  }}
                >
                  {item.price}
                </Typography>

                <Button
                  variant="outlined"
                  sx={{
                    borderColor: "pink",
                    color: "pink",
                    textTransform: "none",
                    fontWeight: "bold",
                    "&:hover": {
                      borderColor: "#ff4081",
                      backgroundColor: "#ffe6f0",
                      color: "#ff4081",
                    },
                  }}
                >
                  Buy
                </Button>
              </Box>
            </CardContent>
          </Card>
        ))}
      </Box>

      <Modal open={open} onClose={handleClose}>
        <Box
          sx={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            bgcolor: "white",
            p: 4,
            borderRadius: 7,
            boxShadow: 24,
            width: 700,
            maxHeight: { md: "90vh", lg: "80vh", xs: "90vh" },
            overflowY: "hidden",
          }}
        >
          {selectedFlower && (
            <Box sx={{ display: "flex", flexDirection: { xs: "column", sm: "row" }, gap: 3 }}>
              <Box sx={{ flex: 1 }}>
                <Box
                  component="img"
                  src={selectedFlower.img}
                  alt={language === "en" ? selectedFlower.nameEN : selectedFlower.nameKA}
                  sx={{ width: 350, height: 400, objectFit: "cover" }}
                />

                <Typography variant="h6" sx={{ mt: 2, color: "crimson" }}>
                  Price: {selectedFlower.price}
                </Typography>

                <Typography variant="h6" sx={{ mt: 1 }}>
                  Rating:
                </Typography>
                <Rating value={4.5} precision={0.5} readOnly sx={{ fontSize: 45 }} />
              </Box>

              <Box sx={{ flex: 2 }}>
                <Typography variant="h4" gutterBottom sx={{ fontWeight: "bold" }}>
                  {language === "en" ? selectedFlower.nameEN : selectedFlower.nameKA}
                </Typography>

                <Typography sx={{ mb: 3, color: "gray" }}>
                  This flower is perfect for any occasion, with vibrant colors and a lovely fragrance.
                </Typography>

                <Box sx={{ display: "flex", gap: 2, flexWrap: "wrap", flexDirection: "column" }}>
                  <Button variant="contained" color="primary" sx={{ borderRadius: 10, height: 45 }}>
                    Buy Now
                  </Button>
                  <Button variant="outlined" color="primary" sx={{ borderRadius: 10, height: 45 }}>
                    Add to Cart
                  </Button>
                </Box>
              </Box>
            </Box>
          )}
        </Box>
      </Modal>
    </>
  );
};

export default MainContent;
