"use client"

import {
  Box,
  Button,
  Card,
  CardContent,
  CardMedia,
  IconButton,
  Modal,
  Rating,
  Typography,
} from "@mui/material";
import { useRef, useState } from "react";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import ArrowForwardIcon from "@mui/icons-material/ArrowForward";
import CloseIcon from "@mui/icons-material/Close"; 

const flowers = [
  { name: "·Éï·Éê·É†·Éì·Éò", price: "15$", img: "roses.jpg" },
  { name: "·É®·É†·Éù·É®·Éê·Éú·Éò", price: "10$", img: "lily.jpg" },
  { name: "·É¢·Éò·É¢·Éê", price: "12$", img: "tulip.jpg" },
  { name: "·Éí·Éï·Éò·É†·Éò·Éö·Éê", price: "11$", img: "daisy.jpg" },
  { name: "·É•·É†·Éò·Éñ·Éê·Éú·Éó·Éî·Éõ·Éê", price: "9$", img: "Chrysanthemum.jpg" },
  { name: "·Éõ·Éò·ÉÆ·Éê·Éô·Éò", price: "18$", img: "Carnation.jpg" },
  { name: "·Éö·Éê·Éï·Éê·Éú·Éì·Éê", price: "6$", img: "Lavender.jpg" },
  { name: "·Éù·É†·É•·Éò·Éì·Éî·Éê", price: "11$", img: "Orchid.jpg" },
  { name: "·Éõ·Éñ·Éî·Éê·Éß·Éï·Éê·Éï·Éò·Éö·Éê", price: "12$", img: "Sunflower.jpg" },
];

const BestSellers = () => {
  const scrollRef = useRef<HTMLDivElement>(null);
  const [open, setOpen] = useState(false);
  const [selectedFlower, setSelectedFlower] = useState<any>(null);

  const scroll = (direction: "left" | "right") => {
    if (scrollRef.current) {
      const scrollAmount = direction === "left" ? -300 : 300;
      scrollRef.current.scrollBy({ left: scrollAmount, behavior: "smooth" });
    }
  };

  const handleOpen = (flower: any) => {
    setSelectedFlower(flower);
    setOpen(true);
  };

  const handleClose = () => setOpen(false);

  return (
    <Box
      sx={{
        width: "100%",
        height: 600,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        gap: 5,
        position: "relative",
      }}
    >
      <Typography letterSpacing={5} color="secondary" sx={{ fontWeight: 700, fontFamily: "serif", fontSize: {xs: 30, md: 60} }}>
        BEST SELLERS:
      </Typography>

      {/* Left Arrow */}
      <IconButton
        disableFocusRipple
        disableRipple
        onClick={() => scroll("left")}
        sx={{
          position: "absolute",
          left: 10,
          top: "50%",
          transform: "translateY(-50%)",
          zIndex: 2,
          backgroundColor: "white",
          boxShadow: 2,
          "&:hover": { backgroundColor: "#ffe6f0" },
        }}
      >
        <ArrowBackIcon sx={{ fontSize: 40, p: 1 }} />
      </IconButton>

      {/* Scrollable Box */}
      <Box
        ref={scrollRef}
        sx={{
          display: "flex",
          overflowX: "auto",
          overflowY: "hidden",
          scrollbarWidth: "none",
          msOverflowStyle: "none",
          width: "90%",
          gap: 2,
          p: 1,
          scrollBehavior: "smooth",
          "&::-webkit-scrollbar": { display: "none" },
        }}
      >
        {flowers.map((item, index) => (
          <Card
            onClick={() => handleOpen(item)}
            key={index}
            sx={{
              flexShrink: 0,
              width: 300,
              height: 400,
              borderRadius: 4,
              boxShadow: 4,
              transition: "0.3s ease",
              "&:hover": {
                transform: "scale(1.03)",
                boxShadow: 10,
              },
            }}
          >
            <CardMedia component="img" height="230" image={item.img} alt={item.name} />
            <CardContent>
              <Typography
                variant="h6"
                gutterBottom
                sx={{ fontWeight: "bold", fontFamily: "Georgia, serif" }}
              >
                {item.name}
              </Typography>

              <Box
                sx={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "center",
                }}
              >
                <Typography
                  variant="h6"
                  sx={{ color: "crimson", fontWeight: "bold", fontSize: "1.2rem" }}
                >
                  {item.price}
                </Typography>

                <Button
                  variant="outlined"
                  sx={{
                    borderColor: "pink",
                    color: "pink",
                    textTransform: "none",
                    fontWeight: "bold",
                    "&:hover": {
                      borderColor: "#ff4081",
                      backgroundColor: "#ffe6f0",
                      color: "#ff4081",
                    },
                  }}
                >
                  Buy
                </Button>
              </Box>
            </CardContent>
          </Card>
        ))}
      </Box>

      {/* Right Arrow */}
      <IconButton
        disableFocusRipple
        disableRipple
        onClick={() => scroll("right")}
        sx={{
          position: "absolute",
          right: 10,
          top: "50%",
          transform: "translateY(-50%)",
          zIndex: 2,
          backgroundColor: "white",
          boxShadow: 2,
          "&:hover": { backgroundColor: "#ffe6f0" },
        }}
      >
        <ArrowForwardIcon sx={{ fontSize: 40, p: 1 }} />
      </IconButton>

      {/* Modal */}
      <Modal open={open} onClose={handleClose}>
  <Box
    sx={{
      position: "absolute",
      top: "50%",
      left: "50%",
      transform: "translate(-50%, -50%)",
      width: { xs: 350, md: 600 },
      bgcolor: "white",
      borderRadius: 4,
      boxShadow: 24,
      p: 4,
      outline: "none",
      display: "flex",
      alignItems: "center",
      flexDirection: "column",
    }}
  >
    {/* ‚ùå Exit Button */}
    <IconButton
      disableFocusRipple
      disableRipple
      onClick={handleClose}
      sx={{
        position: "absolute",
        top: 0,
        right: 0,
      }}
    >
      <CloseIcon sx={{
        fontSize: 50,
        p: 1,
        color: "grey",
        "&:hover": {
          color: "black"
        }
        }}/>
    </IconButton>

    {/* Modal Content */}
    {selectedFlower && (
      <>
        <Typography variant="h5" sx={{ fontWeight: "bold", mb: 2 }}>
          {selectedFlower.name}
        </Typography>
        <CardMedia
          component="img"
          image={selectedFlower.img}
          alt={selectedFlower.name}
          sx={{
            mb: 2,
            width: { xs: 300, md: 400 },
            height: { xs: 300, md: 400 },
          }}
        />
        <Rating
          value={4.5}
          precision={0.5}
          readOnly
          sx={{ fontSize: 45, height: 40 }}
        />
        <Typography variant="h6">
          Price: {selectedFlower.price}
        </Typography>
        <Button
          variant="outlined"
          fullWidth
          sx={{
            borderRadius: 10,
            mb: 1,
            height: 50,
            color: "#e91e63",
            borderColor: "#f8bbd0",
            backgroundColor: "#fff0f5",
            transition: "0.3s ease",
            "&:hover": {
              backgroundColor: "#fce4ec",
              borderColor: "#f48fb1",
              color: "#d81b60",
            },
          }}
        >
          buy now
        </Button>
      </>
    )}
  </Box>
</Modal>
    </Box>
  );
};

export default BestSellers;
